import React, { useState } from 'react';
import { BillItem, BillSummary, Person, SplitInfo } from "./models/Model";
import './App.css';
import 'bulma/css/bulma.css'
import { Table, Tr, Td, Thead, Tbody, Input, Container, Button, Title, Label, Tfoot } from "trunx"
import _ from "lodash";

interface AddPersonTextBoxProps {
  onNewPersonAdded(personName: string): any;
}

const AddPersonTextBox: React.FC<AddPersonTextBoxProps> = (props) => {
  const [personName, setPersonName] = useState("");

  return <>
    <Td>
      <Input className="new-person-input" placeholder="New Person" value={personName} onChange={(evt) => { setPersonName(evt.target.value); }} />
    </Td>
    <Td>
      <Button onClick={() => {
        if (personName === "") {
          return;
        }
        props.onNewPersonAdded(personName)
        setPersonName("");
      }}>➕</Button>
    </Td>
  </>
};

interface BillItemRowProps {
  item: BillItem;
  onItemChanged(rowId: number, name: string, amount: number, splitBy: SplitInfo[]): any;
}

const BillItemRow: React.FC<BillItemRowProps> = ({ item, onItemChanged }) => {
  const { rowId, name, amount, splitBy } = item;
  const onNameChanged = (newName: string) => {
    onItemChanged(rowId, newName, amount, splitBy);
  }

  const onAmountChanged = (_newAmount: string) => {
    let newAmount = parseFloat(_newAmount);

    onItemChanged(rowId, name, newAmount, splitBy);
  }

  const onRatioChanged = (person: Person, _newRatio: string) => {
    let newRatio = parseFloat(_newRatio);
    if (isNaN(newRatio)) {
      newRatio = 0;
    }

    console.log(newRatio);

    const newSplitBy = [...splitBy.filter(split => split.person.colId !== person.colId), { person: person, ratio: newRatio }]
    onItemChanged(rowId, name, amount, newSplitBy)
  }

  const orderedSplitItems = _.sortBy(splitBy, "person.colId");

  return (
    <Tr>
      <Td><Input className="item-name-input" value={item.name} placeholder="New item" onChange={(evt) => onNameChanged(evt.target.value)} /></Td>
      <Td><Input className="amount-input" value={item.amount} type="number" onChange={(evt) => onAmountChanged(evt.target.value)} /></Td>
      {orderedSplitItems.map(split => {
        return <Td key={split.person.colId}><Input className="ratio-input" value={split.ratio} type="number" onChange={(evt) => onRatioChanged(split.person, evt.target.value)} /></Td>
      })}
    </Tr>
  );
}

const App = () => {
  const [billItems, setBillItems] = useState<BillItem[]>([]);
  const [people, setPeople] = useState<Person[]>([]);

  const onNewPersonAdded = (personName: string) => {

    const lastColId = _.max(people.map(person => person.colId));
    const newColId = lastColId !== undefined ? lastColId + 1 : 0;
    const newPerson = { colId: newColId, name: personName };

    const updatedBillItems = billItems.map(billItem => {
      return {
        ...billItem,
        splitBy: [...billItem.splitBy, { person: newPerson, ratio: 0 }]
      }
    });

    setBillItems(updatedBillItems);
    setPeople([...people, newPerson])
  }

  const createNewItem = (): BillItem => {
    const lastRowId = _.max(billItems.map(item => item.rowId));
    const newRowId = lastRowId !== undefined ? lastRowId + 1 : 0;

    return {
      rowId: newRowId,
      name: "",
      amount: 0,
      splitBy: people.map(person => { return { person, ratio: 0 } })
    };
  }

  const onRowAdded = () => {
    setBillItems([...billItems, createNewItem()])
  }

  const onItemChanged = (rowId: number, name: string, amount: number, splitBy: SplitInfo[]) => {
    console.log(`onItemChanged with params [${rowId}, ${name}, ${amount}, ${JSON.stringify(splitBy)}]`)

    const found = _.find(billItems, ["rowId", rowId]);
    if (found) {
      const updatedItem: BillItem = { rowId, name, amount, splitBy };
      setBillItems([...billItems.filter(item => item.rowId !== rowId), updatedItem])
    }
  }

  const orderedBillItems = _.sortBy(billItems, "rowId");

  const getSummary = (): BillSummary => {
    const totalAmount = _.sum(billItems.map(item => item.amount));

    const allSplitBy = orderedBillItems.map(item => _.sortBy(item.splitBy, "person.colId").map(split => {
      return split.ratio / _.sum(item.splitBy.map(split => split.ratio));
    }));
    const transposed = _.zip(...allSplitBy);
    const results = transposed.map((ratios) => {

      const results = ratios.map((r, i) => (r !== undefined && !isNaN(r) ? r : 0) * orderedBillItems[i].amount);
      return _.sum(results);

    });

    console.log(results);

    return {
      totalAmount: totalAmount,
      splitTotals: results
    }
  }

  const summary = getSummary();
  const hasSplitErrors = summary.totalAmount !== _.sum(summary.splitTotals);

  const getSplitSummary = (summary: BillSummary) => {
    return <Tr className="summary-row" hasBackgroundDangerLight={hasSplitErrors} hasBackgroundSuccessLight={!hasSplitErrors}>
      <Td><Label>Total</Label></Td>
      <Td><Label>${summary.totalAmount}</Label></Td>
      {summary.splitTotals.map(total => { return <Td><Label>${total.toFixed(2)}</Label></Td> })}
    </Tr>
  }

  return (
    <Container className="root">
      <Title>Split bills</Title>
      <Table.Container>
        <Table>
          <Thead>
            <Td><Label>Item name</Label></Td>
            <Td><Label>Amount</Label></Td>
            {people.map(person => {
              return <Td key={person.colId}>{person.name}</Td>
            })}
            <AddPersonTextBox onNewPersonAdded={onNewPersonAdded} />
          </Thead>
          <Tbody>
            {orderedBillItems.map(item => <BillItemRow key={item.rowId} item={item} onItemChanged={onItemChanged} />)}
          </Tbody>
          <Tfoot>
            <Tr className="add-item-row"><Button onClick={() => onRowAdded()}>➕</Button></Tr>
            {summary.totalAmount > 0 && getSplitSummary(summary)}
          </Tfoot>
        </Table>
      </Table.Container>
    </Container>
  );
}

export default App;
